.ai-planner-img-first {
    position: relative;
    max-width: 100vw;
    background-image: url('https://images.unsplash.com/photo-1510917181191-85abe24b5300?ixlib=rb-4.0.3&ixid=M3wxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8fA%3D%3D&auto=format&fit=crop&w=1171&q=80');
    background-size: cover;
    padding: 25px 10px 10px 10px;
}

.back-button {
    z-index: 10;
    min-width: 35px;
    height: 35px;
    border: 0;
    border-radius: 50%;
}

.back-button > p {
    font-size: 20px;
    color: rgba(0,0,0,0.6);
}

h2 {
    margin-top: 10px;
    color: #ffffff;
}

.ai-planner-next {
    color: rgba(0,0,0,0.6);
}